
		 export default function(
			state = { 
		'TUCalls' : [], 
		'TUPuts' : [],
	     }, action){
		   
		    switch (action.type){
		        case 'GOT_INSTRUMENTS' :
		       
		         
		         return  { ...state, TUCalls: action.payload.call, TUPuts : action.payload.put};
		       

		       
		        default:
		        
		           return state;
		    }
		}


