	  import React ,{Component} from 'react';		
		import { connect } from "react-redux";
   
    import {generateOrders ,getDeribitiveInstrumentFirsCall} from '../action/action.js';
   

		export  class KiteLogin extends Component{

		  constructor(props){
              super(props);
               this.state ={
                TUCalls : '',
                TUPuts : '',
              };
            }


      componentDidMount(){
          this.props.getDeribitiveInstrumentFirsCall();

      }
            


            


		
		  render(){
        //alert(JSON.stringify(this.props.tickCom));

		    return(
		       <div>
		          

            
              <p>welcome to deribit</p>

                <button onClick = {() => this.props.getDeribitiveInstrumentFirsCall()} >Get Instruments</button>



		          
		        </div>
          )
		    }
		}



  function mapStateToProps(state){
      return  {
         DeribitInstrument : state.DeribitInstrument ,  
         
       }
    };


     export default connect(mapStateToProps,{generateOrders ,getDeribitiveInstrumentFirsCall})(KiteLogin);





